import React, { Component, Suspense } from 'react'
import MySQL from '../img/mysql.svg';

const Knowledge = React.lazy(() => import('./Knowledge'));

export default class knowledges extends Component {
    render() {
        return (
            <div className="uk-section uk-section-default uk-text-center" id="Knowledge">
                <div className="uk-container uk-container-xlarge">

                    <h2 className="uk-margin-bottom">CONOCIMIENTOS</h2>
                    <div className="uk-child-width-1-4@m uk-grid-small uk-grid-match uk-flex uk-flex-center" data-uk-grid>
                        <Suspense fallback={<span data-uk-spinner="ratio: 8"></span>}>
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/NET_Core_Logo.svg"
                                title="C#"
                                preferido
                                progress="90">
                            </Knowledge>
                            <Knowledge
                                image={this.props.JavaLogo}
                                title="JAVA"
                                progress="80">
                            </Knowledge>
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/html5-icon.svg"
                                title="HTML5"
                                progress="75"
                            />
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/css.svg"
                                title="CSS3"
                                progress="70"
                            />
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/javascript.svg"
                                title="JAVASCRIPT"
                                preferido
                                progress="85"
                            />
                            <Knowledge
                                image={this.props.PhpLogo}
                                title="PHP"
                                progress="50">
                            </Knowledge>
                           
                           
                            <Knowledge
                                image={this.props.ReactLogo}
                                preferido
                                title="REACT"
                                progress="50">
                            </Knowledge>
                            
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/ajax.svg"
                                title="AJAX"
                                progress="80">
                            </Knowledge>
                          
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/Git_icon.svg"
                                title="GIT"
                                progress="90">
                            </Knowledge>
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/logotipo-de-github.svg"
                                title="GITHUB"
                                progress="90">
                            </Knowledge>
                            <Knowledge
                                image={this.props.SQLServerLogo}
                                title="MYSQL"
                                preferido
                                progress="85">
                            </Knowledge>
                            <Knowledge
                                image={MySQL}
                                title="MYSQL"
                                progress="75">
                            </Knowledge>
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/firebase-seeklogo.com.svg"
                                title="FIREBASE"
                                progress="55">
                            </Knowledge>
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/uikit.svg"
                                title="UIKIT"
                                preferido
                                progress="70">
                            </Knowledge>
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/materialize.svg"
                                title="MATERIALIZE"
                                progress="60">
                            </Knowledge>
                            <Knowledge
                                image="https://nicolasgl.stdcdn.com/portfolio/bootstrap.svg"
                                title="BOOTSTRAP"
                                progress="60">
                            </Knowledge>
                           
                        </Suspense>
                    </div>
                </div>
                <span id="Projects"></span>
            </div>
        )
    }
}
